<!--  家庭档案详情 -->
<template>
  <div class="table-detail-box family-archives-detail">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb>
        <span slot="custom">{{ archivesInfo.userName }}</span>
      </Breadcrumb>
    </div>
    <!-- 表格 -->
    <div class="inner-box">
      <table class="default-table family-table" cellspacing="0" cellspadding="0">
        <tr>
          <th>姓名</th>
          <td>
            <span>{{ archivesInfo.userName || EMPTYTEXT }}</span>
            <template v-if="archivesInfo.totalCount > 0">
              <el-link
                type="primary"
                :underline="false"
                @click="handleView(archivesInfo.recordId, archivesInfo.userName)"
                ><cc-svg-icon icon-class="magnifier"></cc-svg-icon> 查看协议</el-link
              >
            </template>
            <template v-else>
              <el-link type="info" :underline="false"
                ><cc-svg-icon icon-class="magnifier"></cc-svg-icon>查看协议</el-link
              >
            </template>
          </td>
        </tr>
        <tr>
          <th>性别</th>
          <td>{{ archivesInfo.sexTypeStr || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>居民类型</th>
          <td>{{ archivesInfo.personTypeStr || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>年龄</th>
          <td>{{ archivesInfo.age !== null ? archivesInfo.age + '岁' : EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>身份证号</th>
          <td>{{ archivesInfo.idNo || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>机构</th>
          <td>{{ archivesInfo.registerOrganization || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>家庭成员</th>
          <td>
            <template v-if="archivesInfo.familyList">
              <p v-for="(item, index) in archivesInfo.familyList" :key="index">
                <span>{{ item.userName + '（' + item.personRelation + '）' }}</span>
                <template v-if="item.totalCount > 0">
                  <el-link
                    type="primary"
                    :underline="false"
                    @click="handleView(item.recordId, item.userName)"
                    ><cc-svg-icon icon-class="magnifier"></cc-svg-icon> 查看协议</el-link
                  >
                </template>
                <template v-else>
                  <el-link type="info" :underline="false"
                    ><cc-svg-icon icon-class="magnifier"></cc-svg-icon>查看协议</el-link
                  >
                </template>
              </p>
            </template>
            <template v-else>{{ EMPTYTEXT }}</template>
          </td>
        </tr>
        <tr>
          <th>建档医生</th>
          <td>{{ archivesInfo.registerUser || EMPTYTEXT }}</td>
        </tr>
        <tr>
          <th>建档日期</th>
          <td>{{ archivesInfo.createDateStr || EMPTYTEXT }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'family-archives-detail',
  data() {
    return {
      archivesInfo: {} //家庭档案信息
    }
  },
  created() {
    this.getDetailInfo() //获取家庭档案详情
  },
  methods: {
    //去协议列表
    handleView(id, name) {
      this.$router.push({
        path: '/resident-manage/family-archives/agreement-list',
        query: {
          id: id,
          name: name
        }
      })
    },
    //获取家庭档案详情
    getDetailInfo() {
      this.$emit('viewLoad', true)
      this.$http
        .get(
          process.env.VUE_APP_API +
            '/family/person/getDtoByRecordId?recordId=' +
            this.$route.query.id
        )
        .then(res => {
          if (res.code === 200) {
            this.archivesInfo = res.data
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/styles/table-default';
.family-archives-detail {
  .inner-box {
    .family-table {
      width: 800px;
    }
  }
}
</style>
